import React, { useState } from "react";
import NavbarItem from "./NavbarItem";
import styles from "./Navbar.module.css";
import { Link, useLocation, useNavigate } from "react-router-dom";
import { useDispatch, useSelector } from "react-redux";
import { loginActions } from "../../store/store";
const Navbar = (props) => {
  const dispatch = useDispatch();
  const navigate = useNavigate();
  const isLoggedIn = useSelector((state) => state.login.isLoggedIn);
  const username = useSelector((state) => state.login.username);
  const path = useLocation().pathname;
  const [navbarArr, setnavbarArr] = useState([
    {
      type: "Stays",
      icon: "fa-bed",
      active: true,
    },
    {
      type: "Flights",
      icon: "fa-plane",
      active: false,
    },
    {
      type: "Car rentals",
      icon: "fa-car",
      active: false,
    },
    {
      type: "Attractions",
      icon: "fa-bed",
      active: false,
    },
    {
      type: "Airport taxis",
      icon: "fa-taxi",
      active: false,
    },
  ]);
  //Xy dng function iu chnh gi tr active khi nhn vo cc page trn navbar
  //lc v tm page ang active chuyn v false, chuyn thnh true cho i tng mi c click vo
  const pageActiveHandler = function (type) {
    const typeOfNewPage = type;
    const navbarArrChange = structuredClone(navbarArr);
    //find index ca phn t ang c active l true chuyn n thnh false
    const curActiveIndex = navbarArr.findIndex((arr) => arr.active === true);
    navbarArrChange[curActiveIndex].active = false;
    // nhn state truyn ln l type ca phn t c click vo tm ra index ca phn t trong array v chuyn active thnh true
    const newActiveIndex = navbarArr.findIndex(
      (arr) => arr.type === typeOfNewPage
    );
    navbarArrChange[newActiveIndex].active = true;
    // gi hm set State cho arr va c thay i
    setnavbarArr(navbarArrChange);
  };
  const postLogout = async () => {
    const response = await fetch(
      `${process.env.REACT_APP_BACKEND_URL}/user/logout`,
      {
        method: "POST",
        mode: "cors",
        headers: { "Content-Type": "application/json" },
        credentials: "include",
      }
    );
    if (response.ok) {
      dispatch(loginActions.LOGOUT());
    }
  };

  const logoutHandler = () => {
    postLogout();
  };

  //Tr ra JSX theo giao din ca bi yu cu, render cc phn t navBar bng phng thc map ca array.
  return (
    <div className={styles.container}>
      <div className={styles.navbar}>
        <div className={styles.navbar__intro}>
          <Link to="/">Booking Website</Link>
          <div className={styles.navbar__button}>
            {!isLoggedIn && (
              <>
                <Link to="/user?mode=signup">
                  <button>Sign Up</button>
                </Link>
                <Link to="/user?mode=login">
                  <button>Login</button>
                </Link>
              </>
            )}
            {isLoggedIn && (
              <>
                <span>{username}</span>
                <Link to="/transaction">
                  <button>Transactions</button>
                </Link>
                <Link to="/user?mode=login">
                  <button onClick={logoutHandler}>Logout</button>
                </Link>
              </>
            )}
          </div>
        </div>
        {path !== "/user" && (
          <div className={styles.navbar__pages}>
            <ul>
              {navbarArr.map((navbarItem) => {
                return (
                  <NavbarItem
                    key={navbarItem.type + navbarItem.active}
                    typeNav={navbarItem.type}
                    iconNav={navbarItem.icon}
                    activeNav={navbarItem.active}
                    onChangePageActive={pageActiveHandler}
                  />
                );
              })}
            </ul>
          </div>
        )}
      </div>
    </div>
  );
};
export default Navbar;
