import React, { useState } from "react";
import "react-date-range/dist/styles.css"; // main style file
import "react-date-range/dist/theme/default.css"; // theme css file
import { DateRange } from "react-date-range";
import styles from "./DateRangeInput.module.css";
import { format, addDays } from "date-fns";
const DateRageInput = (props) => {
  //khai bo state cho bit ngi dng c ang input hay khng, khi va vo trang ch hin th placeholder l true
  const [placeholder, setPlaceholder] = useState(true);
  // State date range mc nh gi  cho ngi dng  ngy khi hnh l ngy hm nay v ngy kt thc l + 3 ngy
  const [ranges, setRanges] = useState([
    {
      startDate: new Date(),
      endDate: addDays(new Date(), 3),
      key: "selection",
    },
  ]);
  // Function x l khi ngi dng chn  startdate v enddate s t ng popup v hin th ngy ngi dng chn
  const dateRangeHandler = function (ranges) {
    setRanges([ranges.selection]);
    if (ranges.selection.endDate > ranges.selection.startDate) {
      props.finishPick();
      setPlaceholder(false);
      setdatetodate(
        `${format(ranges.selection.startDate, "dd/MM/yyyy")} - ${format(
          ranges.selection.endDate,
          "dd/MM/yyyy"
        )}`
      );
      props.getDateRange(
        `${format(ranges.selection.startDate, "dd/MM/yyyy")} - ${format(
          ranges.selection.endDate,
          "dd/MM/yyyy"
        )}`
      );
    }
  };
  // khai bo phn t dateRange  dng iu kin hin th trong JSX, nu gi tr isOpenSub(trng thi m popup modal date) l true th s hin th modal cn nu khng s tt modal
  let dateRange = (
    <DateRange
      editableDateInputs={true}
      moveRangeOnFirstSelection={false}
      className="date"
      minDate={new Date()}
      onChange={dateRangeHandler}
      ranges={ranges}
    />
  );
  // Khai bo state cha string date hin th sau khi ngi dng chn xong
  const [datetodate, setdatetodate] = useState(
    `${format(ranges[0].startDate, "dd/MM/yyyy")} - ${format(
      ranges[0].endDate,
      "dd/MM/yyyy"
    )}`
  );
  //Khai bo  function khi ngi dng nhp input date v khi ngi dng xa
  const changeInputDateHandler = function (event) {
    props.getDateRange(event.target.value);
    setdatetodate(event.target.value);
    //Reset input v trng thi ban u khi ngi dng xa ht input date
    if (event.target.value.trim().length === 0) {
      setPlaceholder(true);
      setRanges([
        {
          startDate: new Date(),
          endDate: addDays(new Date(), 3),
          key: "selection",
        },
      ]);
    }
  };
  //JSX section ny tr ra th icon th input v 1 div hin th popup modal chn date range
  return (
    <div className={`${styles.dateRange} ${styles.inputcontainer}`}>
      <i
        className={`fa-solid fa-calendar ${
          !placeholder ? styles.iconActive : ""
        }`}
      ></i>
      <input
        type="text"
        className="inputDate"
        placeholder={
          placeholder
            ? `${format(ranges[0].startDate, "dd/MM/yyyy")} - ${format(
                ranges[0].endDate,
                "dd/MM/yyyy"
              )}  `
            : ""
        }
        onChange={changeInputDateHandler}
        value={!placeholder ? datetodate : ""}
      />
      <div className={styles.calendar}>{props.isOpenSub && dateRange}</div>
    </div>
  );
};
export default DateRageInput;
